<template>
  <PageBody style="padding:0">
    <div class="list-container">
      <ul class="list-type">
        <router-link tag="li" to="/show/approval/waiting" active-class="primary-color">待审核</router-link>
        <router-link tag="li" to="/show/approval/finish" active-class="primary-color">已审核</router-link>
      </ul>
      <div class="query-body">
        <Select style="width:200px;margin-right: 20px" v-if="$route.params.type==='finish'" :clearable="true"
                :value="$route.query.examine" @on-change="handleExamine">
          <Option value="1">已同意</Option>
          <Option value="2">已拒绝</Option>
        </Select>
        <Input size="default" prefix="ios-search" placeholder="昵称／个签关键字搜索" style="width: 200px" :clearable="true"
               :value="$route.query.keyword" @on-change="handleSearch"/>
      </div>
      <div class="list-body" v-loading="isLoading">
        <p v-if="!listData.length" class="empty-list">
          <span v-if="$route.query.keyword">没有匹配信息</span>
          <span v-else>暂无数据</span>
        </p>
        <div v-else class="list-table">
          <el-table
            :header-cell-style="{'text-align': 'center'}"
            style="text-align: center"
            height="100%"
            :data="listData"
            border
            @row-click="handleClick">
            <el-table-column
              prop="index"
              width="92"
              label="图片">
              <template slot-scope="scope">
                <Popover popper-class="image-popover" trigger="hover">
                  <img slot="reference" class="list-image" :src="scope.row.image">
                  <img :src="scope.row.image" class="hover-image">
                </Popover>
              </template>
            </el-table-column>
            <el-table-column
              prop="nickname"
              label="用户昵称">
            </el-table-column>
            <el-table-column
              prop="signature"
              width="206"
              label="个签（36个字符）">
              <template slot-scope="scope">
                <div v-if="String(scope.row.signature).length>12">
                  <Popover popper-class="image-popover" trigger="hover">
                    <div slot="reference">{{String(scope.row.signature).substr(0,12)}}...</div>
                    <div style="max-width: 240px;word-break: break-all">{{scope.row.signature}}</div>
                  </Popover>
                </div>
                <div v-else>{{scope.row.signature}}</div>
              </template>
            </el-table-column>
            <el-table-column
              prop="createTime"
              width="173"
              label="提交时间">
            </el-table-column>
            <el-table-column
              prop="sex"
              width="64"
              label="性别">
            </el-table-column>
            <el-table-column
              v-if="$route.params.type==='waiting'"
              width="118"
              label="操作">
              <template slot-scope="scope">
                <span class="operate-btn primary-color" @click.stop="handleAgree(scope.row.id)">同意</span>
                <span class="operate-btn delete-btn" @click.stop="handleRefuse(scope.row.id)">拒绝</span>
              </template>
            </el-table-column>
            <el-table-column
              v-if="$route.params.type==='finish'"
              width="118"
              label="操作">
              <template slot-scope="scope">
                <span v-if="Number(scope.row.examine)===0" style="color:#A8A8A8">待审核</span>
                <span v-if="Number(scope.row.examine)===1" style="color:#A8A8A8">已同意</span>
                <span v-if="Number(scope.row.examine)===2" style="color:#A8A8A8">已拒绝</span>
                <span class="operate-btn delete-btn" @click.stop="handleDelete(scope.row.id)">删除</span>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <div class="paging-body">
        <Page
          :total="this.page.total"
          :current="this.page.pageNum"
          :page-size="this.page.pageSize"
          show-elevator show-total show-sizer :transfer="false"
          @on-change="pageNumChange"
          @on-page-size-change="pageSizeChange"
        />
      </div>
      <PictureDetail :visible="picture" @close="picture = false"/>
    </div>
  </PageBody>
</template>
<script>
import PictureList from 'views/components/PictureList'
import PictureDetail from './components/PictureDetail'
import { getApprovalData, setApprovalAgree, setApprovalRefuse, deleteApproval } from 'api/show'
import list from '@/mixins/list'
import debounce from 'lodash/debounce'
import { Popover } from 'element-ui'
import { mapActions } from 'vuex'

export default {
  components: {PictureList, PictureDetail, Popover},
  mixins: [list],
  data () {
    return {
      query: {},
      isLoading: false,
      listData: [],
      picture: false,
      page: {
        pageNum: 1,
        pageSize: 10,
        total: 0
      }
    }
  },
  computed: {
    sex: function () {
      switch (this.$route.params.type) {
        case 'all':
          return 1
        case 'male':
          return 2
        case 'female':
          return 3
      }
    }
  },
  methods: {
    handleClick (picture) {
      this.picture = picture
    },
    handleAgree (id) {
      this.$confirm({
        mode: 'longText',
        title: '确定提示',
        brief: '确定同意后，终端机会员秀将同步显示该照片信息！ 且审核结果不可修改！！',
        confirmText: '确定同意',
        confirm: (dialog) => {
          dialog.loading = true
          setApprovalAgree({id: id}).then(res => {
            if (Number(res.code) === 200) {
              dialog.destroy()
              this.listRefresh()
              this.getApprovalNum()
            } else {
              dialog.loading = false
              this.$message({
                message: res.message,
                type: 'warning'
              })
            }
          })
        }
      })
    },
    handleRefuse (id) {
      this.$confirm({
        mode: 'longText',
        title: '确定提示',
        brief: '确定拒绝后，终端机会员秀将不显示该照片信息！ 且审核结果不可修改！！',
        confirmText: '确定拒绝',
        confirm: (dialog) => {
          dialog.loading = true
          setApprovalRefuse({id: id}).then(res => {
            if (Number(res.code) === 200) {
              dialog.destroy()
              this.listRefresh()
              this.getApprovalNum()
            } else {
              dialog.loading = false
              this.$message({
                message: res.message,
                type: 'warning'
              })
            }
          })
        }
      })
    },
    handleDelete (id) {
      this.$confirm({
        title: '确定提示',
        brief: '确定删除此审核记录吗？',
        confirm: (dialog) => {
          dialog.loading = true
          deleteApproval({id: id}).then(res => {
            if (Number(res.code) === 200) {
              dialog.destroy()
              this.listRefresh()
            } else {
              dialog.loading = false
              this.$message({
                message: res.message,
                type: 'warning'
              })
            }
          })
        }
      })
    },
    getList (data = {}) {
      this.isLoading = true
      let query = {
        type: this.$route.params.type,
        pageSize: 10,
        pageNum: 1,
        keyword: ''
      }
      this.query = Object.assign({}, query, data)
      getApprovalData(this.query).then(res => {
        if (Number(res.code) === 200) {
          this.listData = res.data
          res.pageNum && (this.page.pageNum = Number(res.pageNum))
          res.pageSize && (this.page.pageSize = Number(res.pageSize))
          res.total && (this.page.total = Number(res.total))
        } else {
          this.listData = []
          this.$message({
            message: res.message,
            type: 'warning'
          })
        }
      }).finally(() => {
        this.isLoading = false
      })
    },
    handleSearch: debounce(function (e) {
      this.listQuery({
        keyword: e.target.value,
        pageNum: 1
      })
    }, 1000),
    handleExamine (value) {
      this.listQuery({
        examine: value,
        pageNum: 1
      })
    },
    pageNumChange (pageNum) {
      this.listQuery({
        pageNum: String(pageNum)
      })
    },
    pageSizeChange (pageSize) {
      this.listQuery({
        pageSize: String(pageSize)
      })
    },
    ...mapActions([
      'getApprovalNum'
    ])
  }
}
</script>
<style lang='stylus' scoped>
  .list-container
    width 100%
    height 100%
    display flex
    flex-direction column
    align-items stretch
  .query-body
    padding 0 20px
    height 72px
    display flex
    flex-direction row
    align-items center
  .paging-body
    border-top 1px solid #EAEAEA
    height 60px
    width 100%
    padding-right 20px
    display: flex
    justify-content flex-end
    align-items center
  .list-body
    flex 1
    overflow auto
  .list-table
    width 100%
    height 100%
  .add-btn
    font-size 12px
    width 108px
  .empty-list
    width 100%
    height 100%
    display flex
    align-items center
    justify-content center
  .list-type
    height 52px
    display flex
    align-items stretch
    border-bottom 1px solid #D8D8D8
    & > li
      width 96px
      cursor pointer
      display flex
      justify-content center
      align-items center
  .list-image
    width 25px
    height 30px
    border-radius 4px
    background #F0F0F0
  .delete-btn
    color #F25F5A
    margin-left 30px
  .operate-btn
    cursor pointer
  .hover-image
    width 120px
</style>
<style lang='stylus'>
  .image-popover.el-popper
    background #666666
    border-color #666666
    color white
  .image-popover.el-popper[x-placement^=bottom] .popper__arrow
    border-bottom-color #666666
  .image-popover.el-popper[x-placement^=bottom] .popper__arrow::after
    border-bottom-color #666666
</style>



